import React, { Component } from "react";
import BenableItems from "./BenableItems";
import benableQuestions from './benableQuestions';
import "./Benable.css";
import lifeicon from './life.png';
import nolifeicon from './nolife.png'
import congrats from './congrats.gif'

class Benable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      correct_answers: [],
      questionId: 0,
      question: "",
      answers: [],
      lives: 0,
      questionsLeft: [],
      banked: 0,
      counter: -1,
      allDone: false
    };

    this.addAnswer = this.addAnswer.bind(this);
    this.finishQuestion = this.finishQuestion.bind(this);
  }

  componentDidMount() {
    const inds = [...Array(benableQuestions.length).keys()];
    this.setState({
      questionsLeft: inds,
      banked: 0
    });
    setTimeout(() => this.setNextQuestion(), 300);
    console.log(benableQuestions)
  }

  addAnswer(e) {
    if (this._inputElement.value !== "") {
      const newItem = this._inputElement.value.toUpperCase().trim();

      var correct = false;
      var i;
      for (i = 0; i < this.state.answers.length; i++) {
        var correct_answer_obj = this.state.answers[i]
        if(correct_answer_obj.content.includes(newItem)) {
          const c = this.state.correct_answers
          this.setState({
              correct_answers: c.concat(newItem)
          });
          correct = true;
          break;
        }
      }

      if(!correct){
        if(this.state.lives === 0){
          this.finishQuestion(false);
        }else{
          this.setState({
            lives: 0
          })
        }
      }

      this._inputElement.value = "";
    }

    e.preventDefault();
  }

  finishQuestion(banked) {
    if(banked){
      this.setState({
        banked: this.state.banked + [0,500,1000,2500,5000,10000,15000][this.state.correct_answers.length]
      });
    }else{
      this.setState({
        correct_answers: []
      })
    }
    this.setState({
      finishedQ: true
    });
  }


  toggleShowAnswers = () => {this.setState({showAnswers: !this.state.showAnswers});}


  toggleFinished = () => {this.setState({finishedQ: !this.state.finishedQ});this.setNextQuestion();}


  setNextQuestion() {
    const counter = this.state.counter;
    const qsRemaining = this.state.questionsLeft;

    if(qsRemaining.length > 0){
      var nextQId = qsRemaining[Math.floor(Math.random() * qsRemaining.length)];
      var filtered = qsRemaining.filter(function(value, index, arr){ return value !== nextQId;});
      console.log(filtered);
      this.setState({
        counter: counter + 1,
        questionId: nextQId,
        question: benableQuestions[nextQId].question,
        answers: benableQuestions[nextQId].answers,
        correct_answers: [],
        questionsLeft: filtered,
        lives: 1,
        showAnswers: false
      });
    }else{
      this.setState({
        allDone: true
      });
    }
  }

  renderBenable() {
    if (this.state.correct_answers.length === 6) {
      this.finishQuestion(true)
    }else if (this.state.correct_answers.length > 1){
      var showBank = true;
    }else {
      var showBank = false;
    }
    const bankvalues = [0,500,1000,2500,5000,10000,15000];
    return (
      <div>
        <h2>Question {this.state.counter + 1} - {this.state.question}</h2>
        <div className="header">
          <form onSubmit={this.addAnswer}>
            <input ref={(a) => this._inputElement = a}
                  placeholder="Enter Answer">
            </input>
            <button type="submit">Submit</button>
            <button type="input" disabled={!showBank} onClick={this.finishQuestion}>Bank</button>
            &nbsp;&nbsp;&nbsp;&nbsp;<strong>Banked: £{this.state.banked + bankvalues[this.state.correct_answers.length]}</strong>
            <img src={(this.state.lives===1) ? lifeicon : nolifeicon} alt="lifeicon"></img>
          </form>
        </div>
        <BenableItems possibles={this.state.answers} entries={this.state.correct_answers} result={false} />
      </div>
    );
  }

  renderRes(){
    const bankvalues = [0,500,1000,2500,5000,10000,15000];
    return (
      <div className="result">
        {this.state.correct_answers.length === 6 ? <img src={congrats} alt="WOW!"/> : <h2>Room to improve!</h2>}
        <h3>That round you banked £{bankvalues[this.state.correct_answers.length]}</h3>
        <h3>So far you've played {benableQuestions.length - this.state.questionsLeft.length} question(s)
          and banked a total of £{this.state.banked}</h3>
        <button type="input" onClick={this.toggleShowAnswers}>Show/Hide Answers</button>
        <button type="input" onClick={this.toggleFinished}>Next Question</button>
        {this.state.showAnswers ? <BenableItems possibles={this.state.answers} entries={this.state.correct_answers} result={true}/>
        : <div></div>}
      </div>
    );
    
  }

  renderFinished(){
    return(
      <div className="result">
        <h2>You've completed all the questions - we're always posting more though!</h2>
        <h2>Email ben.armstrong@live.co.uk if you have any suggestions</h2>
      </div>
    )
  }

  render() {
    return (
      <div className="benableMain">
        {(this.state.allDone === true) ? this.renderFinished() :
          (this.state.finishedQ) ? this.renderRes() : this.renderBenable()}
      </div>
    );
  }
}

export default Benable;