import React, { Component } from "react";

class BenableItems extends Component {
  createPanels(c,a,d) {
    const returnobj = [];
    for (var i = 0; i <= a.length-1; i++) {
      var answer_obj = a[i];
      var answer_found = false;
      for (var j = 0; j <= answer_obj.content.length-1; j++) {
        if(c.includes(answer_obj.content[j])){
          returnobj.push(<div
                            key={answer_obj.order}
                            className="zone"
                            style={{backgroundColor: 'green'}}
                            ><strong>{answer_obj.content[j]}</strong>
                          </div>);
          answer_found = true;
        }
      }
      if(!answer_found){
        if(d){
          returnobj.push(<div key={answer_obj.order} className="zone">{answer_obj.content[0]}</div>);
        }else{
          returnobj.push(<div key={answer_obj.order} className="zone">{answer_obj.order + 1}</div>);
        }
      }
    }
    return returnobj;
  }

  render() {
    var corrects = this.props.entries;
    var allPanels = this.props.possibles;
    var displayAnswers = this.props.result;
    var panels = this.createPanels(corrects,allPanels,displayAnswers)

    return (

       <div className={displayAnswers ? "answers" : "pyramid"}>
          {panels}
       </div>
    );
  }
};

export default BenableItems;